.manage-connection-content {
    padding: 20px;
    margin-left: 350px; 
    width: calc(100% - 350px);
    /*font-weight: bold;*/
    font-size: large;

}
.add-connection-type-container {
    margin-top: 20px;
    display: flex;
    justify-content:right-bottom;  
  }

  .connection-link {
    color: #007bff;
    text-decoration: underline;
    cursor: pointer;
  }
  
  .connection-link:hover {
    color: #0056b3;
  }
  
  
  .add-connection-type-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: large;
    font-weight: bold;
  }
  
  .add-connection-type-button:hover {
    background-color: #0056b3;
  }
  

