   /* Navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.5rem;
    padding: 0.5rem 1rem 0 1rem;

  }
  
  .navbarBrand {
    font-weight: bolder;
    font-size: 1.5625rem;
    margin-left: 2rem;
  }

  .wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .description {
    font-size: 0.995rem; /* Slightly smaller font size for the description */
    color: rgb(44, 41, 41); /* Different color to distinguish from the name */
    margin-left: 2rem;
  }

  .description2{
    font-size: 0.995rem; /* Slightly smaller font size for the description */
    color: rgb(44, 41, 41); /* Different color to distinguish from the name */
    /* margin-left: 2rem; */
    position: absolute;
    margin-top: inherit;
  }


 .navbarLinks{
    list-style: none;
    display: flex;
    gap: 1rem;
    margin-left: auto;
    
 }
  
  .navbarFirstLink {
    list-style: none;
    display: flex;
    
  }

  .navbarSecondLink {
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .navbarThirdLink {
    list-style: none;
    display: flex;
    position: relative;
  }
  
  .navbarFirstLink a {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    margin: 0 2rem;
    font-size: 1.25rem;
  }

  .navbarSecondLink li {
    margin-bottom: 2rem; /* Add space between the Home and Admin links */
  }

  .navbarSecondLink a {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    margin: 0 2rem;
    font-size: 1.25rem;
  }

  .navbarThirdLink a {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    margin: 0 2rem;
    font-size: 1.25rem;
  }
  
  .navbarLinks img {
    height: 50px;
    width: auto;
    margin-right: 3rem;
  }
  
  /* Hero Container */
  .heroContainer {
    margin-top: 2rem;
    margin-left: 5rem;
    width: 50%;
  }
  
  .newLocker {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem; /* Add margin to separate from allLockers */
    font-size: 1.45rem;
  }
  
  .newLocker button {
    background-color: #007bff;
    color: #fff;
    width: auto;
    border: none;
    padding: 1rem 0.75rem;
    border-radius: 0.25rem;
    cursor: pointer;
    font-weight: bolder;
    /* padding: 0.5rem 0.25rem;  */
    max-width: 200px;
  }
  
  .allLockers {
    border: 2px solid rgb(107, 120, 231);
    padding: 1rem;
    max-height: 500px; /* Set a max height for scrolling */
    overflow-y: auto; /* Enable vertical scrolling */
  }
  
  .page1-locker {
    border: 2px solid rgb(107, 120, 231);
    padding: 1rem;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5rem;
    font-size: 1.45rem;
  }
  
  .page1-locker  button{
    font-weight: bolder;
    padding: 0.5rem 1rem;
    cursor: pointer;
   
  }

