.viewlockerconnections {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  padding: 1rem;
  margin-bottom: 1rem;
  position: relative; /* Ensure the container is positioned relatively */
}
.Lockertracker {
  position: absolute;
  right: 2rem; /* Adjust as needed for spacing */
  bottom: 1rem; /* Adjust as needed for spacing */
}

.info-button,
.tracker {
  margin-left: 2rem; /* Increased spacing between buttons */
}

.info-button {
  background-color: white; /* White background color for the button */
  color: black; /* Black text color */
  border: 2px solid black; /* Black border */
  border-radius: 50%; /* Make the button round */
  padding: 0.5rem; /* Increase padding for better size */
  cursor: pointer; /* Pointer cursor on hover */
  display: inline-flex; /* Center the "i" within the circle */
  align-items: center; /* Align items vertically */
  justify-content: center; /* Align items horizontally */
  width: 2rem; /* Set width to ensure a circular shape */
  height: 2rem; /* Set height to ensure a circular shape */
  font-size: 1rem; /* Adjust font size as needed */
}

.info-button:hover {
  background-color: #333; /* Slightly lighter black on hover */
}
