.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.5rem;
    padding: 0.5rem 1rem 0 1rem;

  }
  
  .navbarBrand {
    font-weight: bolder;
    font-size: 1.5625rem;
    margin-left: 2rem;
  }

  .wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .description {
    font-size: 0.995rem; 
    color: rgb(44, 41, 41); 
    margin-left: 2rem;
  }

 .navbarLinks{
    list-style: none;
    display: flex;
    gap: 1rem;
    margin-left: auto;
    
 }
  
  .navbarFirstLink {
    list-style: none;
    display: flex;
    
  }

  .navbarSecondLink {
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .navbarThirdLink {
    list-style: none;
    display: flex;
    position: relative;
  }
  
  .navbarFirstLink a {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    margin: 0 2rem;
    font-size: 1.25rem;
  }

  .navbarSecondLink li {
    margin-bottom: 2rem; /* Add space between the Home and Admin links */
  }

  .navbarSecondLink a {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    margin: 0 2rem;
    font-size: 1.25rem;
  }

  .navbarThirdLink a {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    margin: 0 2rem;
    font-size: 1.25rem;
  }
  
  /* .navbarLinks img {
