.split-view {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.table-container {
    flex: 1;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    margin-right: 20px;
    overflow-x: auto; /* For horizontal scroll */
}

.table-container table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.table-container th, .table-container td {
    padding: 12px;
    border-bottom: 1px solid #ddd;
    text-align: left;
}

.table-container th {
    background-color: #f2f2f2;
    font-weight: bold;
    text-transform: uppercase;
}

.table-container tr:nth-child(even) {
    background-color: #f9f9f9;
}

.table-container tr:hover {
    background-color: #f1f1f1;
}

.table-container input[type="text"], .table-container input[type="date"] {
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.table-container button {
    padding: 8px 12px;
    background-color: #4582a5;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.table-container button:hover {
    background-color: #4885a8;
}
 

.resource-container {
    padding: 20px;
    border-left: 1px solid #ddd;
    margin-left: 20px;
}

.resource-container h3 {
    font-size: 1.5em;
    margin-bottom: 20px;
}

.resource-container .error {
    color: red;
    font-weight: bold;
    margin-bottom: 10px;
}

.resource-container ul {
    list-style-type: none;
    padding: 0;
}

.resource-container li {
    margin-bottom: 10px;
}

.resource-container label {
    font-size: 1.1em;
    display: flex;
    align-items: center;
}

.resource-container input[type="radio"] {
    margin-right: 10px;
    transform: scale(1.2);
    cursor: pointer;
}

.resource-container button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #4582a5;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
}

.resource-container button:hover {
    background-color: #478bb3;
}


