.navbarLockerName, .navbarLockerOwner {
    font-weight: bolder;
    font-size: 1.5625rem;
    margin-left: 2rem;
  }

.descriptionLocker{
    margin: 3rem; 
    font-size: 1.195rem;                          
}

.page4heroContainer {
    border: 2px solid rgb(107, 120, 231);
    display: flex;
    margin: 3rem;
    justify-content: center;
    align-items: center;
    height: calc(80vh - 4rem); /* Adjusting for the navbar height */
    flex-direction: column;
  }

  .page4resourceHeading {
    font-size: 1.5rem;
    margin-bottom: 4rem;
    /* margin-top: 0.1rem; */
    font-weight: bold;
  }
  
  .page4lockerForm {
    
    display: flex;
    flex-direction: column;
    width: 85%; /* Adjust as needed */
    /* max-width: 600px; */
  }
  
  .page4lockerForm label {
    display: flex;
    flex-direction: row;
    margin-bottom: 5rem;
   
  }

  .page4lockerForm input {
    padding: 0.5rem ;
    /* margin-top: 0.5rem; */
    border: 2px solid rgb(107, 120, 231);
    margin-left: 3rem;
    border-radius: 0.25rem; 
    width: 55%;
    
  }
  
  .page4lockerForm button {
    background-color: #007bff;
    color: #fff;
    width: auto;
    border: none;
    padding: 1rem 0.75rem;
    border-radius: 0.25rem;
    cursor: pointer;
    font-weight: bolder;
    /* padding: 0.5rem 0.25rem;  */
    max-width: 200px;

  }

  .page4lockerForm label span {
    margin-right: 5rem; /* Space between label text and input */
    font-size: 1.45rem;
    font-weight: bolder;
    flex: 0.3;
  }
  
  .page4lockerForm button:hover {
    background-color: #0056b3;
  }