.info-button1 {
  background-color: white; 
  color: black; /* Black text color */
  border: 2px solid black; /* Black border */
  border-radius: 50%; /* Round shape */
  padding: 0.5rem; /* Padding to ensure size */
  cursor: pointer; /* Pointer cursor on hover */
  display: inline-flex; /* Center the "i" within the circle */
  align-items: center; /* Align items vertically */
  justify-content: center; /* Align items horizontally */
  width: 2rem; /* Width to make it circular */
  height: 2rem; /* Height to make it circular */
  font-size: 1rem; /* Font size of the "i" */
  margin-left: 2rem; /* Adjust the spacing as needed */
}

.info-button1:hover {
  background-color: #333; /* Darker background on hover */
  color: white; /* White text color on hover */
}